import React, { SyntheticEvent, useEffect } from 'react';

import { Switch, Route, useHistory, useLocation } from 'react-router-dom';

import AppHeader from '../app-header/app-header';
import styles from './app.module.css';
import ProtectedRoute from '../protected-route/ProtectedRoute';
import {
    HomePage,
    LoginPage,
    RegisterPage,
    NotFoundPage,
    ForgotPasswordPage,
    ResetPasswordPage,
    ProfilePage,
    Ingredient,
    OrderFeedPage,
    OrderPage,
} from '../../pages';
import Modal from '../modal/modal';
import { useDispatch } from '../../services/hooks';
import IngredientDetails from '../ingredient-details/ingredient-details';
import { getUserInfo } from '../../services/actions/auth';
import { getCookie } from '../../utils';
import { getIngredients } from '../../services/actions/burger-ingredients';
import { TLocationState } from './app-types';

function App() {
    const history = useHistory();
    const location: TLocationState = useLocation();
    const dispatch = useDispatch();

    useEffect(() => {
        const token = getCookie('accessToken');
        dispatch(getIngredients());
        dispatch(getUserInfo(token));
    }, [dispatch]);

    const back = (e: KeyboardEvent | SyntheticEvent) => {
        e.stopPropagation();
        history.goBack();
    };

    const action = history.action === 'PUSH' || history.action === 'REPLACE';
    const modalIngredientOpen = action && location.state && location.state.background;

    return (
        <>
            <AppHeader />
            <main className={styles.main}>
                <Switch location={modalIngredientOpen || location}>
                    <Route path="/react-burger/" exact={true}>
                        <HomePage />
                    </Route>
                    <ProtectedRoute path="/react-burger/login" exact={true}>
                        <LoginPage />
                    </ProtectedRoute>
                    <ProtectedRoute path="/react-burger/register" exact={true}>
                        <RegisterPage />
                    </ProtectedRoute>
                    <ProtectedRoute path="/react-burger/forgot-password" exact={true}>
                        <ForgotPasswordPage />
                    </ProtectedRoute>
                    <ProtectedRoute path="/react-burger/reset-password" exact={true}>
                        <ResetPasswordPage />
                    </ProtectedRoute>
                    <ProtectedRoute path="/react-burger/profile" exact={true}>
                        <ProfilePage />
                    </ProtectedRoute>
                    <Route path="/react-burger/ingredients/:id" exact={true}>
                        <Ingredient />
                    </Route>
                    <Route path="/react-burger/feed" exact={true}>
                        <OrderFeedPage />
                    </Route>
                    <Route>
                        <NotFoundPage />
                    </Route>
                </Switch>
                {modalIngredientOpen && (
                    <Route path="/react-burger/ingredients/:id">
                        <Modal onClose={back}>
                            <IngredientDetails />
                        </Modal>
                    </Route>
                )}
            </main>
        </>
    );
}

export default App;
