import React, { FC } from 'react';

import { Redirect, Route, useLocation } from 'react-router-dom';
import { useSelector } from '../../services/hooks';
import { CommonStateType } from '../../services/reducers/reducers-types';

type TProtectedRouteProps = { path: string; exact: boolean };

const ProtectedRoute: FC<TProtectedRouteProps> = ({ children, path, ...rest }) => {
    const user = useSelector((state: CommonStateType) => state.auth.user);
    const location = useLocation();

    const profilePages: string = '/react-burger/profile' || '/react-burger/profile/orders';

    if (
        user &&
        (path === '/react-burger/login' || path === '/react-burger/register' || path === '/react-burger/forgot-password' || path === '/react-burger/reset-password')
    ) {
        return (
            <Redirect
                to={{
                    pathname: '/',
                    state: location,
                }}
            />
        );
    }

    return (
        <Route
            {...rest}
            render={() => (!user && path === profilePages ? <Redirect to={{ pathname: '/react-burger/login' }} /> : children)}
        />
    );
};

export default ProtectedRoute;
